                            line-height: 84px;
                            text-align: center;
                            font-size:36px;
                            -webkit-border-radius: 10px;
                            -moz-border-radius: 10px;
                            border-radius: 10px;
                            font-weight: 100;
                            letter-spacing: 2px;
                            cursor:pointer;
                        }
                        .sure-btn{
                            float:left;
                            background-color: #0074c2;
                            color:#fff;
                            border:1px solid #0074c2;

